// ============================================================================
// ⚙️ Settings Dashboard - Hybrid Detection + Tier Management
// ============================================================================
// קובץ: components/admin/SettingsDashboard.tsx

import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import {
  Settings,
  Zap,
  Shield,
  Users,
  Save,
  RefreshCw,
  AlertTriangle,
  CheckCircle,
  DollarSign,
  Clock,
  Cpu,
  Edit3,
  RotateCcw
} from 'lucide-react';
import { toast } from 'sonner';
import { apiGet, apiPut, apiDelete } from '../../utils/api';
import { logger } from '../../utils/logger';
import HybridControlSettings from './HybridControlSettings';

// ============================================================================
// LLM Model Configuration Types
// ============================================================================
interface LLMModel {
  id: string;
  name: string;
  provider: string;
  cost: string;
  recommended: boolean;
}

interface LLMTierConfig {
  default_model: string;
  fallback_models: string[];
  task_models: Record<string, string>;
  source?: string;
  updated_by?: string;
}

interface LLMConfigData {
  config: Record<string, LLMTierConfig>;
  available_models: LLMModel[];
  task_types: string[];
  tiers: string[];
}

interface TierConfig {
  tier: string;
  display_name: string;
  rate_limits: {
    cases_per_day: number;
    cases_per_month: number;
    concurrent_cases: number;
    requests_per_minute: number;
  };
  token_limits: {
    max_tokens_per_case: number;
    max_tokens_per_day: number;
    max_tokens_per_month: number;
  };
  features: {
    priority_processing: boolean;
    advanced_analysis: boolean;
    api_access: boolean;
    export_formats: string[];
  };
  pricing: {
    monthly_price_cents: number;
    per_case_price_cents: number;
  };
}

const TIER_ORDER = ['free', 'basic', 'professional', 'enterprise', 'admin'];

// Task type display names
const TASK_TYPE_NAMES: Record<string, string> = {
  contradiction_detection: 'זיהוי סתירות',
  legal_analysis: 'ניתוח משפטי',
  claim_extraction: 'חילוץ טענות',
  entity_extraction: 'חילוץ ישויות',
  strategy_building: 'בניית אסטרטגיה',
  insight_generation: 'יצירת תובנות',
  chat: 'צ\'אט',
  rag: 'RAG',
  ocr: 'OCR'
};

export default function SettingsDashboard() {
  const navigate = useNavigate();
  const location = useLocation();

  // Determine active tab from URL or default to 'hybrid'
  const getActiveTabFromUrl = (): 'hybrid' | 'tiers' | 'llm' => {
    const hash = location.hash.replace('#', '');
    if (hash === 'tiers') return 'tiers';
    if (hash === 'llm') return 'llm';
    return 'hybrid';
  };

  const [activeTab, setActiveTab] = useState<'hybrid' | 'tiers' | 'llm'>(getActiveTabFromUrl());
  const [tiers, setTiers] = useState<TierConfig[]>([]);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [editingTier, setEditingTier] = useState<string | null>(null);
  const [editedConfig, setEditedConfig] = useState<Partial<TierConfig>>({});

  // LLM Config State
  const [llmConfig, setLlmConfig] = useState<LLMConfigData | null>(null);
  const [llmLoading, setLlmLoading] = useState(false);
  const [editingLlmTier, setEditingLlmTier] = useState<string | null>(null);
  const [customModelInput, setCustomModelInput] = useState('');

  // Sync tab state with URL hash
  useEffect(() => {
    const newTab = getActiveTabFromUrl();
    if (newTab !== activeTab) {
      setActiveTab(newTab);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.hash]);

  // Update URL hash when tab changes
  const changeTab = (tab: 'hybrid' | 'tiers' | 'llm') => {
    setActiveTab(tab);
    navigate(`/admin/settings#${tab}`, { replace: true });
  };

  useEffect(() => {
    if (activeTab === 'tiers') {
      loadTierConfigs();
    } else if (activeTab === 'llm') {
      loadLlmConfig();
    }
  }, [activeTab]);

  // Load LLM Configuration
  const loadLlmConfig = async () => {
    setLlmLoading(true);
    try {
      const data = await apiGet('/api/admin/llm-config', { showErrorToast: false });
      setLlmConfig(data);
    } catch (error) {
      logger.error('Failed to load LLM config:', error);
      toast.error('שגיאה בטעינת הגדרות מודלים');
    } finally {
      setLlmLoading(false);
    }
  };

  // Update LLM model for a tier
  const updateLlmModel = async (tier: string, model: string, customModel?: string) => {
    setSaving(true);
    try {
      const payload = { model, custom_model: customModel || null };
      await apiPut(`/api/admin/llm-config/${tier}/model`, payload, { showErrorToast: false });
      toast.success(`מודל ברירת מחדל עודכן ל-${customModel || model}`);
      await loadLlmConfig();
      setEditingLlmTier(null);
      setCustomModelInput('');
    } catch (error) {
      logger.error('Failed to update LLM model:', error);
      toast.error('שגיאה בעדכון מודל');
    } finally {
      setSaving(false);
    }
  };

  // Update task-specific model
  const updateTaskModel = async (tier: string, taskType: string, model: string) => {
    setSaving(true);
    try {
      await apiPut(`/api/admin/llm-config/${tier}/task/${taskType}`, { model }, { showErrorToast: false });
      toast.success(`מודל ל-${TASK_TYPE_NAMES[taskType] || taskType} עודכן`);
      await loadLlmConfig();
    } catch (error) {
      logger.error('Failed to update task model:', error);
      toast.error('שגיאה בעדכון מודל');
    } finally {
      setSaving(false);
    }
  };

  // Reset tier to defaults
  const resetLlmTier = async (tier: string) => {
    if (!window.confirm(`האם לאפס את הגדרות ${tier} לברירת מחדל?`)) return;

    setSaving(true);
    try {
      await apiDelete(`/api/admin/llm-config/${tier}`, { showErrorToast: false });
      toast.success(`הגדרות ${tier} אופסו לברירת מחדל`);
      await loadLlmConfig();
    } catch (error) {
      logger.error('Failed to reset LLM config:', error);
      toast.error('שגיאה באיפוס הגדרות');
    } finally {
      setSaving(false);
    }
  };

  const loadTierConfigs = async () => {
    setLoading(true);
    try {
      const data = await apiGet('/api/admin/tiers/all', { showErrorToast: false });
      setTiers(data.tiers || []);
    } catch (error) {
      logger.error('Failed to load tier configs:', error);
      toast.error('שגיאה בטעינת הגדרות Tier');
    } finally {
      setLoading(false);
    }
  };

  const handleEditTier = (tier: TierConfig) => {
    setEditingTier(tier.tier);
    setEditedConfig(tier);
  };

  const handleSaveTier = async () => {
    if (!editingTier || !editedConfig) return;

    setSaving(true);
    try {
      await apiPut(`/api/admin/tiers/${editingTier}`, editedConfig, { showErrorToast: false });
      toast.success(`הגדרות ${editingTier} נשמרו בהצלחה`);
      setEditingTier(null);
      setEditedConfig({});
      await loadTierConfigs();
    } catch (error) {
      logger.error('Failed to save tier config:', error);
      toast.error('שגיאה בשמירת הגדרות');
    } finally {
      setSaving(false);
    }
  };

  const updateEditedField = (path: string, value: any) => {
    setEditedConfig(prev => {
      const newConfig = { ...prev };
      const keys = path.split('.');
      let current: any = newConfig;

      for (let i = 0; i < keys.length - 1; i++) {
        if (!current[keys[i]]) current[keys[i]] = {};
        current = current[keys[i]];
      }

      current[keys[keys.length - 1]] = value;
      return newConfig;
    });
  };

  const formatCurrency = (cents: number) => `$${(cents / 100).toFixed(2)}`;

  return (
    <div className="p-6" dir="rtl">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <div className="flex items-center gap-4">
            <div className="w-16 h-16 bg-purple-500/20 border-2 border-purple-500 rounded-xl flex items-center justify-center">
              <Settings size={32} className="text-purple-400" />
            </div>
            <div>
              <h1 className="text-4xl font-bold text-white mb-2">⚙️ הגדרות מערכת</h1>
              <p className="text-white/70">ניהול Hybrid Detection ותצורת Tier</p>
            </div>
          </div>
        </div>

        {/* Tabs */}
        <div className="flex gap-2 mb-6 border-b border-white/20 pb-2">
          <button
            onClick={() => changeTab('hybrid')}
            className={`px-6 py-3 rounded-t-lg font-medium transition flex items-center gap-2 ${
              activeTab === 'hybrid'
                ? 'bg-purple-600 text-white'
                : 'bg-white/5 text-purple-300 hover:bg-white/10'
            }`}
          >
            <Zap size={20} />
            Hybrid Detection
          </button>
          <button
            onClick={() => changeTab('tiers')}
            className={`px-6 py-3 rounded-t-lg font-medium transition flex items-center gap-2 ${
              activeTab === 'tiers'
                ? 'bg-purple-600 text-white'
                : 'bg-white/5 text-purple-300 hover:bg-white/10'
            }`}
          >
            <Shield size={20} />
            ניהול Tiers
          </button>
          <button
            onClick={() => changeTab('llm')}
            className={`px-6 py-3 rounded-t-lg font-medium transition flex items-center gap-2 ${
              activeTab === 'llm'
                ? 'bg-purple-600 text-white'
                : 'bg-white/5 text-purple-300 hover:bg-white/10'
            }`}
          >
            <Cpu size={20} />
            ניהול מודלים
          </button>
        </div>

        {/* Hybrid Detection Tab */}
        {activeTab === 'hybrid' && (
          <HybridControlSettings />
        )}

        {/* Tier Management Tab */}
        {activeTab === 'tiers' && (
          <div className="space-y-6">
            {loading ? (
              <div className="text-center py-12">
                <RefreshCw className="animate-spin mx-auto text-purple-400" size={48} />
                <p className="text-white/70 mt-4">טוען הגדרות Tier...</p>
              </div>
            ) : (
              <>
                {/* Tiers Overview */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                  {TIER_ORDER.map(tierName => {
                    const tier = tiers.find(t => t.tier === tierName);
                    if (!tier) return null;

                    const isEditing = editingTier === tier.tier;
                    const config = isEditing ? editedConfig as TierConfig : tier;

                    return (
                      <div
                        key={tier.tier}
                        className={`bg-white/10 backdrop-blur-lg rounded-lg border-2 transition-all ${
                          isEditing ? 'border-purple-500' : 'border-white/20'
                        }`}
                      >
                        <div className="p-6">
                          {/* Tier Header */}
                          <div className="flex items-center justify-between mb-4">
                            <div>
                              <h3 className={`text-xl font-bold capitalize ${
                                tier.tier === 'enterprise' ? 'text-purple-400' :
                                tier.tier === 'professional' ? 'text-blue-400' :
                                tier.tier === 'basic' ? 'text-green-400' :
                                tier.tier === 'admin' ? 'text-red-400' :
                                'text-gray-400'
                              }`}>
                                {tier.display_name || tier.tier}
                              </h3>
                              <p className="text-white/50 text-sm">{tier.tier}</p>
                            </div>
                            {!isEditing ? (
                              <button
                                onClick={() => handleEditTier(tier)}
                                className="px-3 py-1 bg-blue-600 hover:bg-blue-700 text-white rounded text-sm transition-colors"
                              >
                                ערוך
                              </button>
                            ) : (
                              <div className="flex gap-2">
                                <button
                                  onClick={() => {
                                    setEditingTier(null);
                                    setEditedConfig({});
                                  }}
                                  className="px-3 py-1 bg-gray-600 hover:bg-gray-700 text-white rounded text-sm transition-colors"
                                >
                                  ביטול
                                </button>
                                <button
                                  onClick={handleSaveTier}
                                  disabled={saving}
                                  className="px-3 py-1 bg-green-600 hover:bg-green-700 disabled:bg-gray-600 text-white rounded text-sm transition-colors flex items-center gap-1"
                                >
                                  {saving ? <RefreshCw className="w-3 h-3 animate-spin" /> : <Save className="w-3 h-3" />}
                                  שמור
                                </button>
                              </div>
                            )}
                          </div>

                          {/* Pricing */}
                          <div className="mb-4 p-3 bg-white/5 rounded-lg">
                            <div className="flex items-center gap-2 mb-2">
                              <DollarSign size={16} className="text-green-400" />
                              <span className="text-white/70 text-sm font-medium">תמחור</span>
                            </div>
                            {isEditing ? (
                              <div className="space-y-2">
                                <input
                                  type="number"
                                  value={(config.pricing?.monthly_price_cents || 0) / 100}
                                  onChange={(e) => updateEditedField('pricing.monthly_price_cents', parseFloat(e.target.value) * 100)}
                                  className="w-full px-3 py-1 bg-white/10 border border-white/20 rounded text-white text-sm"
                                  placeholder="מחיר חודשי"
                                />
                              </div>
                            ) : (
                              <p className="text-white text-lg font-bold">
                                {formatCurrency(tier.pricing.monthly_price_cents)}/חודש
                              </p>
                            )}
                          </div>

                          {/* Rate Limits */}
                          <div className="mb-4">
                            <div className="flex items-center gap-2 mb-3">
                              <Clock size={16} className="text-yellow-400" />
                              <span className="text-white/70 text-sm font-medium">מגבלות שימוש</span>
                            </div>
                            <div className="space-y-2 text-sm">
                              {isEditing ? (
                                <>
                                  <div className="flex items-center justify-between">
                                    <span className="text-white/60">תיקים ליום:</span>
                                    <input
                                      type="number"
                                      value={config.rate_limits?.cases_per_day || 0}
                                      onChange={(e) => updateEditedField('rate_limits.cases_per_day', parseInt(e.target.value))}
                                      className="w-20 px-2 py-1 bg-white/10 border border-white/20 rounded text-white text-xs"
                                    />
                                  </div>
                                  <div className="flex items-center justify-between">
                                    <span className="text-white/60">תיקים לחודש:</span>
                                    <input
                                      type="number"
                                      value={config.rate_limits?.cases_per_month || 0}
                                      onChange={(e) => updateEditedField('rate_limits.cases_per_month', parseInt(e.target.value))}
                                      className="w-20 px-2 py-1 bg-white/10 border border-white/20 rounded text-white text-xs"
                                    />
                                  </div>
                                  <div className="flex items-center justify-between">
                                    <span className="text-white/60">תיקים מקבילים:</span>
                                    <input
                                      type="number"
                                      value={config.rate_limits?.concurrent_cases || 0}
                                      onChange={(e) => updateEditedField('rate_limits.concurrent_cases', parseInt(e.target.value))}
                                      className="w-20 px-2 py-1 bg-white/10 border border-white/20 rounded text-white text-xs"
                                    />
                                  </div>
                                </>
                              ) : (
                                <>
                                  <div className="flex items-center justify-between">
                                    <span className="text-white/60">תיקים ליום:</span>
                                    <span className="text-white font-medium">{tier.rate_limits.cases_per_day}</span>
                                  </div>
                                  <div className="flex items-center justify-between">
                                    <span className="text-white/60">תיקים לחודש:</span>
                                    <span className="text-white font-medium">{tier.rate_limits.cases_per_month}</span>
                                  </div>
                                  <div className="flex items-center justify-between">
                                    <span className="text-white/60">תיקים מקבילים:</span>
                                    <span className="text-white font-medium">{tier.rate_limits.concurrent_cases}</span>
                                  </div>
                                </>
                              )}
                            </div>
                          </div>

                          {/* Token Limits */}
                          <div className="mb-4">
                            <div className="flex items-center gap-2 mb-3">
                              <Zap size={16} className="text-blue-400" />
                              <span className="text-white/70 text-sm font-medium">מגבלות טוקנים</span>
                            </div>
                            <div className="space-y-2 text-sm">
                              {isEditing ? (
                                <>
                                  <div className="flex items-center justify-between">
                                    <span className="text-white/60">לתיק:</span>
                                    <input
                                      type="number"
                                      value={config.token_limits?.max_tokens_per_case || 0}
                                      onChange={(e) => updateEditedField('token_limits.max_tokens_per_case', parseInt(e.target.value))}
                                      className="w-24 px-2 py-1 bg-white/10 border border-white/20 rounded text-white text-xs"
                                    />
                                  </div>
                                  <div className="flex items-center justify-between">
                                    <span className="text-white/60">ליום:</span>
                                    <input
                                      type="number"
                                      value={config.token_limits?.max_tokens_per_day || 0}
                                      onChange={(e) => updateEditedField('token_limits.max_tokens_per_day', parseInt(e.target.value))}
                                      className="w-24 px-2 py-1 bg-white/10 border border-white/20 rounded text-white text-xs"
                                    />
                                  </div>
                                </>
                              ) : (
                                <>
                                  <div className="flex items-center justify-between">
                                    <span className="text-white/60">לתיק:</span>
                                    <span className="text-white font-medium">{tier.token_limits.max_tokens_per_case.toLocaleString()}</span>
                                  </div>
                                  <div className="flex items-center justify-between">
                                    <span className="text-white/60">ליום:</span>
                                    <span className="text-white font-medium">{tier.token_limits.max_tokens_per_day.toLocaleString()}</span>
                                  </div>
                                </>
                              )}
                            </div>
                          </div>

                          {/* Features */}
                          <div>
                            <div className="flex items-center gap-2 mb-3">
                              <CheckCircle size={16} className="text-green-400" />
                              <span className="text-white/70 text-sm font-medium">תכונות</span>
                            </div>
                            <div className="space-y-1 text-xs">
                              <div className="flex items-center gap-2">
                                {tier.features.priority_processing ? (
                                  <CheckCircle size={12} className="text-green-400" />
                                ) : (
                                  <AlertTriangle size={12} className="text-gray-500" />
                                )}
                                <span className={tier.features.priority_processing ? 'text-white' : 'text-white/40'}>
                                  עיבוד מועדף
                                </span>
                              </div>
                              <div className="flex items-center gap-2">
                                {tier.features.advanced_analysis ? (
                                  <CheckCircle size={12} className="text-green-400" />
                                ) : (
                                  <AlertTriangle size={12} className="text-gray-500" />
                                )}
                                <span className={tier.features.advanced_analysis ? 'text-white' : 'text-white/40'}>
                                  ניתוח מתקדם
                                </span>
                              </div>
                              <div className="flex items-center gap-2">
                                {tier.features.api_access ? (
                                  <CheckCircle size={12} className="text-green-400" />
                                ) : (
                                  <AlertTriangle size={12} className="text-gray-500" />
                                )}
                                <span className={tier.features.api_access ? 'text-white' : 'text-white/40'}>
                                  גישת API
                                </span>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </div>

                {/* Info Section */}
                <div className="bg-blue-500/10 border border-blue-500/30 rounded-lg p-6">
                  <div className="flex items-start gap-3">
                    <Users className="text-blue-400 flex-shrink-0" size={24} />
                    <div>
                      <h3 className="text-white font-bold mb-2">אודות ניהול Tiers</h3>
                      <p className="text-blue-200 text-sm mb-2">
                        כאן ניתן לנהל את הגדרות התמחור, מגבלות השימוש והתכונות של כל רמת מנוי (Tier).
                      </p>
                      <ul className="text-blue-200 text-sm space-y-1">
                        <li>• <strong>Rate Limits:</strong> מגבלות על מספר התיקים והבקשות</li>
                        <li>• <strong>Token Limits:</strong> מגבלות על צריכת טוקנים</li>
                        <li>• <strong>Features:</strong> תכונות זמינות לכל רמת מנוי</li>
                        <li>• <strong>Pricing:</strong> תמחור חודשי ולפי שימוש</li>
                      </ul>
                    </div>
                  </div>
                </div>
              </>
            )}
          </div>
        )}

        {/* LLM Models Tab */}
        {activeTab === 'llm' && (
          <div className="space-y-6">
            {llmLoading ? (
              <div className="text-center py-12">
                <RefreshCw className="animate-spin mx-auto text-purple-400" size={48} />
                <p className="text-white/70 mt-4">טוען הגדרות מודלים...</p>
              </div>
            ) : llmConfig ? (
              <>
                {/* Available Models Info */}
                <div className="bg-gradient-to-r from-blue-500/10 to-purple-500/10 border border-blue-500/30 rounded-lg p-6">
                  <h3 className="text-white font-bold mb-4 flex items-center gap-2">
                    <Cpu className="text-blue-400" size={20} />
                    מודלים זמינים
                  </h3>
                  <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-3">
                    {llmConfig.available_models.map(model => (
                      <div
                        key={model.id}
                        className={`p-3 rounded-lg border ${
                          model.recommended
                            ? 'bg-green-500/10 border-green-500/50'
                            : 'bg-white/5 border-white/20'
                        }`}
                      >
                        <div className="flex items-center gap-2 mb-1">
                          {model.recommended && (
                            <span className="text-xs bg-green-500 text-white px-1.5 py-0.5 rounded">מומלץ</span>
                          )}
                          <span className="text-xs text-white/50">{model.provider}</span>
                        </div>
                        <p className="text-white font-medium text-sm">{model.name}</p>
                        <p className="text-white/50 text-xs">{model.cost}</p>
                      </div>
                    ))}
                  </div>
                </div>

                {/* Per-Tier Configuration */}
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                  {TIER_ORDER.map(tier => {
                    const tierConfig = llmConfig.config[tier];
                    if (!tierConfig) return null;

                    const isEditing = editingLlmTier === tier;

                    return (
                      <div
                        key={tier}
                        className={`bg-white/10 backdrop-blur-lg rounded-lg border-2 transition-all ${
                          isEditing ? 'border-purple-500' : 'border-white/20'
                        }`}
                      >
                        <div className="p-6">
                          {/* Tier Header */}
                          <div className="flex items-center justify-between mb-4">
                            <div>
                              <h3 className={`text-xl font-bold capitalize ${
                                tier === 'enterprise' ? 'text-purple-400' :
                                tier === 'professional' ? 'text-blue-400' :
                                tier === 'basic' ? 'text-green-400' :
                                tier === 'admin' ? 'text-red-400' :
                                'text-gray-400'
                              }`}>
                                {tier}
                              </h3>
                              {tierConfig.source === 'admin' && (
                                <span className="text-xs text-yellow-400">מותאם אישית</span>
                              )}
                            </div>
                            <div className="flex gap-2">
                              {!isEditing ? (
                                <>
                                  <button
                                    onClick={() => setEditingLlmTier(tier)}
                                    className="p-2 bg-blue-600 hover:bg-blue-700 text-white rounded transition-colors"
                                    title="ערוך"
                                  >
                                    <Edit3 size={16} />
                                  </button>
                                  <button
                                    onClick={() => resetLlmTier(tier)}
                                    className="p-2 bg-gray-600 hover:bg-gray-700 text-white rounded transition-colors"
                                    title="אפס לברירת מחדל"
                                  >
                                    <RotateCcw size={16} />
                                  </button>
                                </>
                              ) : (
                                <button
                                  onClick={() => {
                                    setEditingLlmTier(null);
                                    setCustomModelInput('');
                                  }}
                                  className="px-3 py-1 bg-gray-600 hover:bg-gray-700 text-white rounded text-sm transition-colors"
                                >
                                  סגור
                                </button>
                              )}
                            </div>
                          </div>

                          {/* Default Model */}
                          <div className="mb-4 p-3 bg-white/5 rounded-lg">
                            <p className="text-white/70 text-sm mb-2">מודל ברירת מחדל:</p>
                            {isEditing ? (
                              <div className="space-y-2">
                                <select
                                  value={tierConfig.default_model}
                                  onChange={(e) => updateLlmModel(tier, e.target.value)}
                                  disabled={saving}
                                  className="w-full px-3 py-2 bg-white/10 border border-white/20 rounded text-white text-sm"
                                >
                                  {llmConfig.available_models.map(m => (
                                    <option key={m.id} value={m.id}>{m.name} ({m.cost})</option>
                                  ))}
                                </select>
                                <div className="flex gap-2">
                                  <input
                                    type="text"
                                    value={customModelInput}
                                    onChange={(e) => setCustomModelInput(e.target.value)}
                                    placeholder="או הזן שם מודל מותאם..."
                                    className="flex-1 px-3 py-2 bg-white/10 border border-white/20 rounded text-white text-sm placeholder:text-white/30"
                                  />
                                  {customModelInput && (
                                    <button
                                      onClick={() => updateLlmModel(tier, 'custom', customModelInput)}
                                      disabled={saving}
                                      className="px-3 py-2 bg-green-600 hover:bg-green-700 disabled:bg-gray-600 text-white rounded text-sm"
                                    >
                                      {saving ? <RefreshCw className="w-4 h-4 animate-spin" /> : 'שמור'}
                                    </button>
                                  )}
                                </div>
                              </div>
                            ) : (
                              <p className="text-white font-mono text-lg">{tierConfig.default_model}</p>
                            )}
                          </div>

                          {/* Fallback Models */}
                          <div className="mb-4">
                            <p className="text-white/70 text-sm mb-2">מודלים חלופיים:</p>
                            <div className="flex flex-wrap gap-1">
                              {tierConfig.fallback_models.map((model, idx) => (
                                <span
                                  key={idx}
                                  className="px-2 py-1 bg-white/10 rounded text-xs text-white/80"
                                >
                                  {model}
                                </span>
                              ))}
                            </div>
                          </div>

                          {/* Task-Specific Models */}
                          {isEditing && (
                            <div className="border-t border-white/10 pt-4">
                              <p className="text-white/70 text-sm mb-3">מודלים לפי משימה:</p>
                              <div className="space-y-2 max-h-48 overflow-y-auto">
                                {llmConfig.task_types.map(taskType => (
                                  <div key={taskType} className="flex items-center gap-2">
                                    <span className="text-white/60 text-xs w-28 flex-shrink-0">
                                      {TASK_TYPE_NAMES[taskType] || taskType}:
                                    </span>
                                    <select
                                      value={tierConfig.task_models?.[taskType] || tierConfig.default_model}
                                      onChange={(e) => updateTaskModel(tier, taskType, e.target.value)}
                                      disabled={saving}
                                      className="flex-1 px-2 py-1 bg-white/10 border border-white/20 rounded text-white text-xs"
                                    >
                                      <option value="">ברירת מחדל</option>
                                      {llmConfig.available_models.map(m => (
                                        <option key={m.id} value={m.id}>{m.name}</option>
                                      ))}
                                    </select>
                                  </div>
                                ))}
                              </div>
                            </div>
                          )}

                          {/* Task Models Summary (when not editing) */}
                          {!isEditing && Object.keys(tierConfig.task_models || {}).length > 0 && (
                            <div className="border-t border-white/10 pt-3">
                              <p className="text-white/50 text-xs mb-2">הגדרות מותאמות:</p>
                              {Object.entries(tierConfig.task_models).map(([task, model]) => (
                                <div key={task} className="flex justify-between text-xs">
                                  <span className="text-white/60">{TASK_TYPE_NAMES[task] || task}</span>
                                  <span className="text-white/80 font-mono">{model}</span>
                                </div>
                              ))}
                            </div>
                          )}
                        </div>
                      </div>
                    );
                  })}
                </div>

                {/* Info Section */}
                <div className="bg-purple-500/10 border border-purple-500/30 rounded-lg p-6">
                  <div className="flex items-start gap-3">
                    <Cpu className="text-purple-400 flex-shrink-0" size={24} />
                    <div>
                      <h3 className="text-white font-bold mb-2">אודות ניהול מודלים</h3>
                      <p className="text-purple-200 text-sm mb-2">
                        כאן ניתן להגדיר איזה מודל AI ישמש לכל רמת מנוי ולכל סוג משימה.
                      </p>
                      <ul className="text-purple-200 text-sm space-y-1">
                        <li>• <strong>מודל ברירת מחדל:</strong> המודל שישמש לרוב המשימות</li>
                        <li>• <strong>מודלים חלופיים:</strong> יופעלו אם המודל הראשי נכשל</li>
                        <li>• <strong>מודלים לפי משימה:</strong> אפשר להגדיר מודל שונה לכל סוג פעולה</li>
                        <li>• <strong>מודל מותאם:</strong> אפשר להזין שם מודל חדש שלא ברשימה</li>
                      </ul>
                    </div>
                  </div>
                </div>
              </>
            ) : (
              <div className="text-center py-12 text-white/50">
                <AlertTriangle className="mx-auto mb-4" size={48} />
                <p>לא ניתן לטעון הגדרות מודלים</p>
                <button
                  onClick={loadLlmConfig}
                  className="mt-4 px-4 py-2 bg-purple-600 hover:bg-purple-700 text-white rounded"
                >
                  נסה שוב
                </button>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
}
